.img {
	width: 100%;
	border-radius: 30px;
	box-shadow: 0px 5px 20px 3px rgba(0, 0, 0, 0.28);
}

::-webkit-scrollbar {
	width: 15px;
	cursor: pointer;
}


/* Handle */

::-webkit-scrollbar-thumb {
	background: rgb(204, 0, 255);
	border-radius: 10px;
}


/* Handle on hover */

::-webkit-scrollbar-thumb:hover {
	background: #470250;
}

